body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.styles_statusBar__2DNnl {
  display: flex;
  width: 100%;
  /* height: 42px; */
  border-bottom: #29487d;
  background-color: #4267b2;
  margin-bottom: 20px;
  padding: 5px 0;
}
.styles_statusBar__2DNnl button {
   display: flex;
   background-color: transparent;
   border: 2px solid var(--paletteColor5);
   margin: auto;
   border-radius: 10px;
   padding: 5px 25px;
}
.styles_statusBar__2DNnl img {
    width: 30px;
    height: 30px;
    margin-right: 15px;
    border: 1px solid var(--paletteColor5);
    border-radius: 50%;
    cursor: pointer;
    margin-right: 10px;
}
.styles_statusBar__2DNnl span {
    align-self: center;
    color: white;
}
.styles_composer__1FwBx {
    display: flex;
    position: relative;
    width: 500px;
    height: 160px;
    margin-bottom: 10px;
    padding: 12px;
    border: 1px solid var(--paletteColor4);
    border-radius: 3px;
    background-color: var(--paletteColor2);
}
.styles_composer__1FwBx img {
   width: 40px;
   height: 40px;
   margin-right: 15px;
   -webkit-transition: border .5s ease;
   transition: border .5s ease;
   border: 1px solid var(--paletteColor5);
   border-radius: 50%;
   cursor: pointer;
}

.styles_composer__1FwBx input[type='submit'] {
   position: absolute;
   right: 10px;
   bottom: 10px;
   width: 50px;
   height: 22px;
   -webkit-transition: background-color .2s ease-in;
   transition: background-color .2s ease-in;
   border: 0;
   border-radius: 2px;
   outline: none;
   background-color: var(--paletteColor3);
   color: var(--paletteColor2);
   letter-spacing: 1px;
   cursor: pointer;
}
.styles_composer__1FwBx input[type='submit']:hover {
    background-color: var(--paletteColor7);
}
.styles_composer__1FwBx textarea { 
   width: 410px;
   height: 80px;
   margin-top: 7px;
   -webkit-transition: font-size .5s ease;
   transition: font-size .5s ease;
   border: 0;
   outline: 0;
   font-size: 14px;
   font-weight: 300;
   resize: none;
}
.styles_composer__1FwBx textarea::scrollbar {
    display: none;
}
.styles_composer__1FwBx textarea::-webkit-input-placeholder {
    -webkit-transition: color .4s ease-out;
    transition: color .4s ease-out;
    color: var(--paletteColor5);
}
.styles_composer__1FwBx textarea::-ms-input-placeholder {
    -webkit-transition: color .4s ease-out;
    transition: color .4s ease-out;
    color: var(--paletteColor5);
}
.styles_composer__1FwBx textarea::placeholder {
    -webkit-transition: color .4s ease-out;
    transition: color .4s ease-out;
    color: var(--paletteColor5);
}
.styles_composer__1FwBx textarea:focus::-webkit-input-placeholder {
    color: var(--paletteColor5)
}
.styles_composer__1FwBx textarea:focus::-ms-input-placeholder {
    color: var(--paletteColor5)
}
.styles_composer__1FwBx textarea:focus::placeholder {
    color: var(--paletteColor5)
}
.styles_composer__1FwBx textarea:focus {
    font-size: 24px;
}
.styles_like__2E9oa {
    margin-top: 50px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.styles_icon__R7fu2 {
    position: relative;
    margin-left: 6px;
    padding-top: 2px;
    padding-left: 20px;
    color: var(--paletteColor9);
    font-size: 13px;
    font-weight: 600;
    cursor: default;
}
.styles_icon__R7fu2:hover{
    text-decoration: underline;
    cursor: pointer;
}
.styles_icon__R7fu2::before{
    content: '';
    display: block;
    position: absolute;
    top: -1px;
    left: -2px;
    width: 18px;
    height: 18px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: 0 -18px;
    background-size: 18px;
}

.styles_icon__R7fu2.styles_liked__2Bqjd::before {
    background-position: 0 0;
} 
