@-webkit-keyframes styles_spinner__25Apx {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}
@keyframes styles_spinner__25Apx {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}
.styles_spinner__25Apx {
    position: fixed;
    width: 30px;
    height: 30px;
    border: 3px solid #90949C;
    border-top: 4px solid #F25A41;
    border-radius: 100%;
    top: 5px;
    right: 5px;
    -webkit-animation: styles_spinner__25Apx 0.5s infinite linear;
            animation: styles_spinner__25Apx 0.5s infinite linear;
    z-index: 2;
}
